<template>
	<div>
  <div class="page-title">
    <h3> </h3>
  </div>

  <div class="history-chart">
    <canvas ref="canvas"></canvas>
  </div>
  <Loader v-if="loading"></Loader>
  <p v-else-if="!records.length">  </p>
  <section v-else>
    <HistoryTable :records="items"></HistoryTable>

    <Paginate
      v-model="page"
      :page-count="pageCount"
      :click-handler="pageChangeHandler"
      :prev-text="''"
      :next-text="''"
      :container-class="'pagination'"
      :page-class="'waves-effect'">
    </Paginate>
  </section>
</div>
</template>

<script>
  import HistoryTable from '@/components/HistoryTable'
  import {Doughnut} from 'vue-chartjs'
  import paginationMixin from '@/mixins/pagination.mixin' // 
  export default {
    name: 'history',
    extends: Doughnut,
    mixins: [paginationMixin], //   .       !
    data(){
      return{
        loading: true,
        // categories: [],
        records: []
      }
    },
    components: {
      HistoryTable
    },
    methods: {
     setup(categories){
      this.setupPagination(this.records.map((rec) => {      //this.setupPagination -      paginationMixin
        const title = categories.find((cat) => {
          return cat.id === rec.categoryId
        }).title //  title   
        return {
          ...rec,
          categoryName: title,
          typeClass: rec.type === 'income' ? 'green' : 'red',
          typeText: rec.type === 'income' ? '' : ''
        }
      }))
      
      this.renderChart({
        labels: categories.map(cat => cat.title), //           
        datasets: [{
            label: '  ',
            data: categories.map(c => {
                return this.records.reduce((total, r) => {
                  if(r.categoryId === c.id && r.type === 'outcome'){
                    total += r.amount
                  }
                  return total
                }, 0)
              }),
            backgroundColor: [
                'rgba(255, 99, 132, 0.2)',
                'rgba(54, 162, 235, 0.2)',
                'rgba(255, 206, 86, 0.2)',
                'rgba(75, 192, 192, 0.2)',
                'rgba(153, 102, 255, 0.2)',
                'rgba(255, 159, 64, 0.2)'
            ],
            borderColor: [
                'rgba(255, 99, 132, 1)',
                'rgba(54, 162, 235, 1)',
                'rgba(255, 206, 86, 1)',
                'rgba(75, 192, 192, 1)',
                'rgba(153, 102, 255, 1)',
                'rgba(255, 159, 64, 1)'
            ],
            borderWidth: 1
        }]
    })
     }
    },
    async mounted(){
      // this.records = await this.$store.dispatch('fetchRecords')
      this.records = await this.$store.dispatch('fetchRecords')
      const categories = await this.$store.dispatch('getCategories')

     this.setup(categories)
      this.loading = false
    }
  }
</script>